$blue:#1A519E;
$yalow:#F4CF1B;
$blue2: #1078AD;
$gry:#7F7F7F;
$blue3:#D0E5F0;
$grin:#369D53;

* {
  margin: 0;
  padding: 0;
  font-family: 'Cairo', sans-serif;
  

}

.navebar {
    background-color: $blue3;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: fixed;
    top: 0px;
    width: 100%;
    overflow: hidden;
  
  
    .logo__nav {
      margin-left: 2rem;
      margin-top: 1rem;
    }
  
    .logo__nav2 {
  
  
  
      img {
        margin-left: 10px;
        margin-top: 6px;
      }
  
      border: solid;
      border-width: 1px;
      border-radius: 8px;
      padding-left: 13px;
      padding-right: 13px;
  
    }
  
  
    a {
      color: $blue;
      float: right;
      margin-right: 3rem;
      margin-top: 2rem;
      text-decoration: none;
      font-weight: 700;
      font-size: 20px;
    }
  
    a:hover {
      color: black;
    }
  }
  
  .nav2 {
    margin-top: 7%;
    text-align: center;
    background-color: $yalow;
    color: $blue2 ;
    font-weight: 700;
   
    
  
    h3 {
      color: black;
      font-size: 16px;
    }
    p{
      font-size: 17px;
    }
  }
  